@use '../../styles/mixins' as *;
@use '../../styles/colours' as *;

.favourite-places {
    &__list{
        list-style: none;
        padding: 0;
    }

    &__title {
        margin: 16px;
        font-size: 24px;
    }

    &__link {
        text-decoration: none;
        color: inherit;
        flex-grow: 2;
    }

    &__entry {
        display: flex;
        border-bottom: 1px solid $chalkboard;
        align-items: baseline;
        padding: 6px 16px;
    
        &--headings{
            font-weight: 600;
            display: flex;
            border-bottom: 1px solid $chalkboard;
            align-items: baseline;
            padding: 6px 16px;
            background-color: $main-colour;
            color: $light-script;
            border: none;
        }
    
        &:hover{
            background-color: $light-script;
        }
    }

    &__button {
        @include favButton;
    }
}